body {
  margin: 0 auto;
  font-family: "ProximaNova", "Open Sans", sans-serif;
  color: #111;
  font-size: 16px; }

.container {
  width: 92%;
  margin: 0 auto; }
  @media (min-width: 768px) {
    .container {
      width: 768px; } }
  @media (min-width: 1200px) {
    .container {
      width: 1200px; } }

.title {
  font-family: 'ProximaNova';
  font-size: 40px;
  text-align: center;
  font-weight: 800; }

