.sprite {
  background-image: url("../img/spritesheet.png");
  background-repeat: no-repeat;
  display: block; }

.sprite-avatar {
  width: 24px;
  height: 27px;
  background-position: -5px -5px; }

.sprite-buttonup {
  width: 33px;
  height: 33px;
  background-position: -39px -5px; }

.sprite-instagram {
  width: 19px;
  height: 19px;
  background-position: -82px -5px; }

.sprite-mountain {
  width: 35px;
  height: 22px;
  background-position: -111px -5px; }

.sprite-phonecall {
  width: 14px;
  height: 14px;
  background-position: -82px -37px; }

.sprite-placeholder {
  width: 11px;
  height: 16px;
  background-position: -106px -37px; }

.sprite-question {
  width: 57px;
  height: 58px;
  background-position: -5px -63px; }

.sprite-rope {
  width: 57px;
  height: 58px;
  background-position: -72px -63px; }

.sprite-sign {
  width: 57px;
  height: 58px;
  background-position: -156px -5px; }

.sprite-time {
  width: 19px;
  height: 19px;
  background-position: -139px -73px; }

.sprite-twitter {
  width: 22px;
  height: 18px;
  background-position: -168px -73px; }

.sprite-vk {
  width: 25px;
  height: 15px;
  background-position: -168px -101px; }

.header {
  min-height: 810px;
  margin: 0 auto;
  background-image: url("../img/header-img.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center; }

.header__contacts {
  display: flex;
  width: 100%;
  background-color: #e6e6ef; }

.header__contacts-wrap {
  float: right;
  background-color: #e6e6ef; }

.contacts {
  margin-left: auto;
  justify-content: space-between; }

.header__line {
  display: none; }
  @media (min-width: 768px) {
    .header__line {
      margin-top: 10px;
      display: flex;
      width: 100%;
      align-items: center; } }

.header-nav {
  margin-left: auto; }

.main-nav__item {
  display: inline-block;
  margin-right: 20px;
  font-family: "ProximaNova", "Open Sans", sans-serif;
  font-size: 18px; }

.main-nav__item:last-child {
  margin-right: 0; }

.header__btn {
  margin-top: 10px;
  padding: 12px 25px;
  border: 2px solid #111;
  background-color: #eeeef4;
  margin-left: 25px; }

.logo__img {
  background: center center no-repeat; }

.mountains__description {
  display: none;
  width: 50%;
  margin-top: 0; }
  @media (min-width: 992px) {
    .mountains__description .mountains__description {
      display: block; } }

.actions__container {
  overflow: hidden; }

.actions__foto {
  overflow: hidden;
  margin-top: 80px; }

@media (min-width: 1200px) {
  .actions__list {
    display: flex;
    justify-content: space-between; } }

.actions__item {
  width: 100%;
  position: relative;
  display: block; }
  @media (min-width: 1200px) {
    .actions__item {
      width: 360px; } }
  .actions__item:hover {
    filter: brightness(60%);
    transition: linear 200ms; }
  .actions__item:hover .link__text {
    display: block; }

.actions__img {
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover; }

.actions__text {
  text-align: left;
  color: #ffffff;
  font-size: 30px;
  font-family: "ProximaNova", "Open Sans", sans-serif;
  font-weight: 600;
  position: absolute;
  bottom: 5%;
  left: 5%; }

.actions__link {
  color: #ffffff;
  position: absolute;
  left: 40%;
  top: 50%; }

.link__text {
  color: #ffffff;
  font-family: 'Open Sans';
  text-decoration: underline;
  display: none; }

.footer {
  background-color: #22313f;
  font-size: 14px;
  font-family: 'OpenSans';
  font-weight: 400; }

iframe {
  width: 100%;
  height: 230px; }

.contacts__item {
  color: #9096a4;
  display: flex;
  flex-direction: column; }

.footer__phone {
  color: #9096a4;
  margin-top: 10px; }

.footer__nav {
  display: flex;
  flex-direction: column;
  color: #9096a4; }

.nav {
  margin-top: 15px; }

.nav__item {
  margin-bottom: 10px; }

.nav__link {
  color: #9096a4; }

.footer__line {
  display: flex;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 20px; }

.footer__socials {
  display: flex; }

.socials__item {
  margin-right: 10px; }

.contacts__item:before {
  position: relative; }

.contacts__item:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  margin: auto; }

.footer__address:before {
  background: url("../img/sprite/placeholder.png") 0 0 no-repeat;
  width: 11px;
  height: 16px; }

.link-toheader {
  margin-left: auto;
  margin-bottom: 5px; }
